/*loader*/
.loaderArea {
  background-color: #616161;
  overflow: hidden;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9999; }

.loader {
  width: 50px;
  height: 50px;
  margin-left: -25px;
  margin-top: -25px;
  position: absolute;
  top: 50%;
  left: 50%;
  border: 2px solid;
  border-color: #fc5f45 #38c695 #b27cf5 #feb960;
  border-radius: 10px;
  animation: border 2s linear infinite -0.15s; }
  .loader::after, .loader::before {
    position: absolute;
    content: "";
    display: block;
    top: 0px;
    left: 0px;
    transform: translate(-50%, -50%);
    width: 50%;
    height: 50%;
    border-radius: 50%;
    animation: spin 2s ease infinite;
    z-index: 15; }
  .loader::after {
    background-color: #fc5f45; }
  .loader::before {
    background-color: #38c695;
    animation-delay: -0.5s; }
  .loader:last-child::after {
    animation-delay: -1s;
    background-color: #b27cf5; }
  .loader:last-child::before {
    animation-delay: -1.5s;
    background-color: #feb960; }

@keyframes spin {
  0%, 100% {
    top: 0;
    left: 0; }
  25% {
    top: 0;
    left: 100%; }
  50% {
    top: 100%;
    left: 100%; }
  75% {
    top: 100%;
    left: 0; } }

@keyframes border {
  0%, 100% {
    border-color: #38c695 #b27cf5 #feb960 #fc5f45; }
  25% {
    border-color: #fc5f45 #38c695 #b27cf5 #feb960; }
  50% {
    border-color: #feb960 #fc5f45 #38c695 #b27cf5; }
  75% {
    border-color: #b27cf5 #feb960 #fc5f45 #38c695; } }

